function checkUsername(str) {
  const len = str.length;
  let char_number = 0;
  if (!str) {
    return false;
  }
  for (const character of str) {
    if (character >= "0" && character <= "9") {
      char_number += 1;
    }
  }
  if (char_number == len) {
    // all char is number
    return "";
  } else {
    return true;
  }
}
